// IIFE to prevent globals
(function($) {

    'use strict';

	var csrfToken = window.csrfToken;

	$('form[name="passwordRecovery"]').submit(function(event) {
		event.preventDefault();

		var $form = $(this),
			url = $form.attr('action') + '?_csrf=' + csrfToken,
			data = $form.serialize();

		$.ajax({
			method: 'POST',
			url: url,
			data: data
		}).done(function() {
			$('#passwordResetPart').hide();
			$('#resetSuccessfull').show();
		}).fail(function(res) {
			var errorText = res.responseText;
			$('#errorMessage').text(errorText).show();
		});
	});

})(window.jQuery);