// IIFE to prevent globals
(function($) {

    'use strict';

	$('#showPasswordForgotPart').click(function(){
	    $('#loginPart').hide();
	    $('#passwordForgotPart').show();
	});

	$('#showLoginPart').click(function(){
	    $('#loginPart').show();
	    $('#passwordForgotPart').hide();
	});

	var csrfToken = window.csrfToken;

	$( '#passwordForgotForm' ).submit(function(event) {

	    event.preventDefault();

	    var $form = $(this),
	        url = $form.attr( 'action' ) + "?_csrf=" + csrfToken,
		    data = $form.serialize();

	    $.ajax({
	        method: 'POST',
	        url: url,
	        data: data
	    }).done(function() {
            $('#passwordForgotPart').hide();
            $('#sendSuccessfull').show();
        }).fail(function(res) {
            var errorMessage = res.responseText;
            $('#sendFailed').text(errorMessage).show();
        });
	});

})(window.jQuery);