// IIFE to prevent globals
(function($) {

	'use strict';

	var s;
	var isTextfieldEmptyJS = {
		settings: {
			selector: 'input[type="text"],input[type="password"],input[type="email"]',
			emptyClass: 'input__text--empty',
			notEmptyClass: 'input__text--not-empty'
		},

		init: function() {
			s = this.settings;
			this.bindUIActions();
		},

		bindUIActions: function() {
			var _that = this,
				isChrome = _that.isChrome();

			$(document).on('keyup paste change input', s.selector, function() {
				_that.checkIfEmpty(this);
			});

			$(function() {
				$(s.selector).each(function() {
					if (isChrome) {
						_that.dirtyWorkaroundForChrome(this);
					}
					_that.checkIfEmpty(this);
				});
			});
		},

		checkIfEmpty: function(element) {
			var $element = $(element);

			if ($element.val() === '') {
				$element.addClass(s.emptyClass).removeClass(s.notEmptyClass);
			} else {
				$element.removeClass(s.emptyClass).addClass(s.notEmptyClass);
			}
		},

		// dirty Workaround for Chrome: https://bugs.chromium.org/p/chromium/issues/detail?id=352527

		dirtyWorkaroundForChrome: function(element) {
			var $element = $(element);

			if (!$element.is('[placeholder]')) {
				$element.attr('placeholder',' ');
			}
		},

		isChrome: function() {
			var isChromium = window.chrome,
				winNav = window.navigator,
				vendorName = winNav.vendor,
				isOpera = winNav.userAgent.indexOf("OPR") > -1,
				isIEedge = winNav.userAgent.indexOf("Edge") > -1,
				isIOSChrome = winNav.userAgent.match("CriOS");

			return !isIOSChrome &&
				isChromium !== null &&
				isChromium !== undefined &&
				vendorName === "Google Inc." &&
				isOpera == false &&
				isIEedge == false;
		}
	};

	isTextfieldEmptyJS.init();

})(window.jQuery);